<template>
	<div >
		<div class="tradingRecord qrcode">
			<div style="height:1px"></div>
			<div class="clearfix topuphead">
				<a @click="$router.go(-1)" class="ago fl"><i class="icon iconfont icon-angle-left"></i></a>
				<router-link tag="a"  :to="{name:'home',params: { busId: busId }}" class="more fr" >
					<a ></a>
				</router-link>
			</div>
			<div class="OfferPay">
				<p>,</p>
			</div>
			<div class="offerPayCom">
				<div class="OfferPayQR">
					<img :src="urlCardPay" width="50%">
				</div>
				<p>,</p>
			</div>
		</div>

	</div>
</template>
<script>
import Lib from '@/assets/js/Lib';
	export default{
		data() {
		    return {
		    	busId:0,
		    	url:'',
		    	urlCardPay:'',
		    	dataKey:'',
		    	urlPay:'',
		    	lendMoney:0,
		    	payTitle:'100,!',
		    	payDesc:',',
		    	payImg:'http://wx.qlogo.cn/mmopen/ib5Ef9ImF1HN9hPsiaBlibGe5iaQDGFtJ4J33IicwtlB8YxSWzfLrmBNqjsdqOUM3MFtJpuZRdpNhwIqguw0D5ibOhxaFKBlsP20De/0'
			}
		},
		methods:{
			loadData:function(){
				var _this=this,json={};
				_this.busId= _this.$route.params.busId;
				_this.dataKey= _this.$route.params.dataKey;
				_this.lendMoney= _this.$route.params.money;
				_this.payTitle=''+_this.lendMoney+',!'
				_this.url=window.location.href;
				_this.urlPay=_this.url.split('#');
				_this.urlPay=_this.urlPay[0]+'#/shareOfferPay/'+_this.busId;
				json["busId"]=_this.busId;
				json["requestUrl"]=_this.urlPay;
				// _this.urlCardNo=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.tuijianQRcode_GET+"?busId="+_this.busId;
				Lib.M.ajax({
					'url':memberAPI.activeAPI.authorizeOrLogin_POST,
	                'type': 'POST',
	                'data':{
	                   "json":json
	                },
	                'success':function (data){
	                	if(data.code==0){
	                		_this.urlCardPay=memberAPI.activeAPI.homeUrl+memberAPI.activeAPI.memberLentImage_GET+"?busId="+_this.busId+"&memberLentKey="+_this.dataKey;
	                		console.log('offerPay',_this.urlCardPay)
	                		Lib.M.ajax({
								'url':memberAPI.activeAPI.wxshareCard_POST,
				                'type': 'POST',
				                'data':{
				                   "json":json
				            	},
				                'success':function (data){
				                	// alert(JSON.stringify(data))
				                	console.log('shareQRCode',data)
				                	if(data.code==0){
				                		wx.config({
											debug: true, // ,apialertpclogpc
										    appId: data.data.appid, // 
										    timestamp: data.data.timestamp, // 
										    nonceStr: data.data.nonce_str, // 
										    signature: data.data.signature,// 1
										    jsApiList: ["onMenuShareAppMessage"] // JSJS2
										});
										wx.ready(function(){
											//
											 wx.onMenuShareAppMessage({
											    title: _this.payTitle, // 
											    link: _this.urlPay, // 
											    desc: _this.payDesc, // 
											    imgUrl: _this.payImg, // 
											    success: function () { 
											        // 
											        Toast({
										            	message:'',
										            	duration:2000
										            });
											    },
											    cancel: function () { 
											        // 
											        Toast({
										            	message:'',
										            	duration:1000
										            });
											    }
											}); 
										});
				                	}
				                }
							})
	                	}
	                	
	                }
				})
			}
		},
		created:function(){
	    	var _this=this;
	    	_this.loadData();
	        
	    }
	}
</script>
<style scope>

</style>


